@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,900;1,700&display=swap');
*{
    font-family:'Poppins', sans-serif;
}
.App{
    overflow-x: hidden;
    /* display: flex; */
    align-items: center;
}
body{
    background-color: #221D23;
    color: #DDB967;
    overflow-x: hidden;
}
.container1{
    padding-left: 90px;
    padding-top: 50px;
    
}
h1{
    margin: 0;
    padding-bottom: 5vh;
    font-size: 48px;
}
.Header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 200px;
}
.ToDo{
    margin-left: 40px;
    width: 100vw;
}
.time{
    /* padding-right: 100px; */
}
.tasksMake{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 250px;
    height: 250px;
    background-color: #D1603D;
    font-size: 96px;
    border-radius: 15px;
    margin-top: 20px;
    transition: all .3s;
    color: antiquewhite;
}
.tasksMake:hover{
    
    cursor: pointer;
    transform: scale(1.1);
}
::placeholder{
    color: antiquewhite;
}
.taskEnter{
    display: flex;
    flex-direction: column;
    background-color: #D1603D;
    width: 1200px;
    height: 600px;
    margin: auto;
    margin-top: 4vh;
    border-radius: 15px;
    padding-left: 5vw;
    padding-top: 5vh;
    color: antiquewhite;
}
.changeTaskHead{
    font-size: 8vw;
    margin-bottom: -7vw;
}
textarea{
    padding-bottom: 150px;
    width: 600px;
    background-color: #ddba6780;
    border-color: #DDB967;
    border-style: solid;
    border-radius: 5px;
    height: 26px;
    color: #fff;

    
}
.actualTaskTime{
    margin-top: -10vw;
    
    
    text-align: center;
    font-size: 20%;
    /* text-size-adjust: 0%; */
    /* color: antiquewhite; */

    margin: 10px;
    margin-bottom: 3vw;
    height: 0px;
    margin-top: 0vh;
    word-wrap:break-word;
    word-break:normal;
}
.TimeDiv{
    margin-top:-4.2vh;
}
.timeText{
    margin-left: 10px;
    margin-bottom: 10px;
}
.tasksNtime{
    display: flex;
    flex-direction: row;
}
.textareaDiv{
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}

.passwordShow{
    display: flex;
    flex-direction: row;
}

button{
    background-color: #DDB967;
    color: white;
    border-style: none;
    border-radius: 5px;
    margin-top: 3vh;
    width: 100px;
    padding-top: 2vh;
    padding-bottom: 2vh;
    transition: all .5s;
}
button:hover{
    cursor: pointer;
    opacity: .8;
}
.timePicker{
    margin-top: -30px;
    margin-bottom: 10px;
    background-color: #DDB967;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: .5vw;
    margin-left: 10px;    
}
.showButton{
    /* height: 35px; */
    margin-top: 10px;
    margin-left: 15px;
    padding-top: 2px;
    padding-bottom: 2px;
}
.taskSect{
    display: flex;
    flex-wrap:wrap ;
    
}
.tasks{
    margin-right: 20px;
    overflow-x: hidden;
    overflow-y: hidden;
    display: flex;
    margin-top: 20px;
    flex-direction: column;
    align-items: center;
    
    width: 250px;
    height: 250px;
    background-color: #D1603D;
    font-size: 96px;
    border-radius: 15px;
    transition: all .3s;
}
.tasks:hover{
    cursor: pointer;
    transform: scale(1.1);
}
.taskHead{
    font-size: 30px;
    margin-top: 10px;
    color: antiquewhite;
    margin-bottom: -6vh;

}

.actualTask{
 

    text-align: center;
    font-size: 20%;
    /* text-size-adjust: 0%; */
    color: antiquewhite;

    margin: 10px;
    padding-top: 10px;
    margin-bottom: -1vw;
    height: 0px;
    margin-top: 1vh;
    word-wrap:break-word;
    word-break:normal;
}
.changeTaskChoice{
    display: flex;
    flex-direction: row;
    
}
#choices{
    display: inline-flex;
    margin-top: 30%;
}
.doneTask{

    /* border: #DDB967; */
    /* border-style: solid; */
    /* border-width: 3px; */
    padding-left: 20px;
    padding-right: 20px;
    color: antiquewhite;
    /* border-radius: 4px; */
    font-size: 15%;    
    margin-top: auto;
    margin-top: 20px;
    margin-left: 10px;
    align-items: flex-end;
}
.logInMainDiv{
    display: flex;
    flex-direction: column;
    background-color: #D1603D;
    width: 100vw;
    height: 100vh;
    /* margin: auto; */
    color: antiquewhite;
    align-items: center;
    justify-content: center;
}
.logInHeader{
    padding: 0px;
    margin:0px auto 3% auto;
}
.inputBOXDiv{
    margin-top: -3%;
    width: 15vw;
    display: flex;
    flex-direction:column;
}
.inputBOX{
    margin-top: 10px;
    width: 100%;
    background-color: #ddba6780;
    border-color: #DDB967;
    border-style: solid;
    border-radius: 5px;
    height: 26px;
    color: #fff;

}
textarea{
    width: 30vw;
    height: 1vh;
    margin-right: 10px;
    overflow-y: hidden;
    
   }
#verifyID{
    width: 70vw;
    text-align: center;
   }
@media (max-width:820px) {
    .TimeDiv{
        margin-top: -25px;
        margin-left: -10px;
    }
    .tasksNtime{
        margin-top: -3vh;
        flex-direction: column;
    }
    .timePicker{
        margin-left: 10vw;
    }
    .taskEnter{
        display: flex;
        align-items: center;
        width: 100vw;
        height: 100vh;
        margin-top: 0px;
        border-radius: 0px;
        padding-top: 3vh;
    }
    .actualTask{
        margin-bottom: -20px;
       }
}
@media (max-width: 500px) {
    
    .taskEnter{
        display: flex;
        align-items: center;
        width: 100vw;
        height: 100vh;
        margin: 0%;
        padding: 0px;
        border-radius: 4px;
        overflow: hidden;
        padding-top: 3vh;
        
    }
    .TimeDiv{
        margin-left: -10px;
    }
    .tasksNtime{
        flex-direction: column;
    }
    .timeText{
        margin-left: 10px;
        margin-bottom: 10px;
    }
    .textareaDiv{
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        
    }
    .timePicker{
        margin-bottom: 10px;
        background-color: #DDB967;
        color: #fff;
        border: none;
        border-radius: 4px;
        padding: 1.6vw;
        margin-left: 10px;    
    }
   #verifyID{
    width: 70vw;
    text-align: center;
   }
    .inputBOXDiv{
        width: 54vw;
    }
   .ToDo{
    margin-right: -100px;
   }
   .Header{
    flex-direction: column;
   }
   .TimeRelated{
    display: flex;
    flex-direction: row;
    margin-top: -40px;
   }
   .time{
margin-right: 45%;

   }
   .taskEnter{
    width: auto;
    margin: 20px;
   }
   textarea{
    width: auto;
    margin-right: 10px;
    margin-bottom: -10px;
   }
   .taskEnterHeader{
    padding: 0px;
   }
   .actualTask{
    margin-top: 20px;
    margin-bottom: -10px;
   }
}
@media screen and (max-width: 414px) {
    .taskEnter{
        display: flex;
        align-items: center;
        width: 100vw;
        height: 100vh;
        margin: 0%;
        padding-top: 3vh;
        border-radius: 0px;
        overflow: hidden;
    }
    .App{
    margin-left: -10px;
    
}
.inputBOXDiv{
    width: 54vw;
}
.tasksNtime{
    flex-direction: column;
}
.TimeDiv{
    margin-left: -10px;
}
.actualTask{
    margin-bottom: -10px;
    
   }
}
@media screen and (max-width: 390px) {
    .taskEnter{
        display: flex;
        align-items: center;
        width: 100vw;
        height: 100vh;
        margin: 0%;
        padding: 0%;
        border-radius: 0px;
        overflow: hidden;
        padding-top: 3vh;
    }
    .App{
    margin-left: -8vw;
    
}
.TimeDiv{
    margin-left: -10px;
}
.inputBOXDiv{
    width: 54vw;
}
.tasksNtime{
    flex-direction: column;
}
.actualTask{
    margin-bottom: -10px;
    
   }
}
